import { _decorator, AudioClip, AudioSource, Component, Node, Toggle } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('AudioToggle')
export class AudioToggle extends Component {
    static instance: AudioToggle;

    @property(AudioClip)
    public clip: AudioClip [] = [];

    @property(Toggle)
    public audioTog: Toggle = null;
    @property(Node)
    public turnOn: Node = null;
    @property(Node)
    public turnOff: Node = null;
    @property(AudioSource)
    public audioSrc: AudioSource = null;
}


